.App {
  display: absolute;
  position: fixed;
  background-color: rgba(79, 206, 235, 0.441);
  min-height: 100vh;
  min-width: 100vw;
}

#title{
  display: flex;
  justify-content: center;
  padding: 1rem;
  font-size: 300%;
  margin-bottom: -1rem;
}

#kitSelector {
  width: fit-content;
  margin: auto;
  margin-bottom: 1rem;
}

.drumKitContainer {
  width: 30rem;
  height: 28rem;
  margin: auto;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto auto auto;
  row-gap: 1rem;
  column-gap: 1rem;
  border: 2px solid orange;
}

.drum {
  margin: auto;
  width: 6rem;
  height: 6rem;
}

img {
  max-width: 5rem;
}

#button1{
  width: fit-content;
  margin: auto;

}